.nav-number {
  -ms-flex-align: center;
      align-items: center;
  background: #fafffd;
  border-right: 2px solid #00D59A;
  display: -ms-flexbox;
  display: flex;
  font-size: 0.8rem;
  height: 2.5rem;
  -ms-flex-pack: center;
      justify-content: center;
  width: 2.5rem;
  -webkit-transition: 0.3s ease;
  -o-transition: 0.3s ease;
  transition: 0.3s ease;
}

.nav-number:last-child {
  border-right: none;
}

.nav-number:hover {
  background-color: rgba(110, 254, 214, 0.5);
  cursor: pointer;
}

.nav-number.current {
  background-color: rgba(110, 254, 214, 1);
  font-weight: bold;
  font-size: 1rem;
}



